.detail_project .swiper-slide .img {
  height: 380px;
  background-size: cover;
  background-position: center;
}

.detail_project .container {
  max-width: 800px;
}

.about_container {
  margin: 10rem 0 0;
}

.swiper-pagination-bullet {
  opacity: 1 !important;
}

.swiper-pagination-bullet {
  -webkit-tap-highlight-color: rgba(201, 224, 253, 0) !important;
  visibility: visible !important;
  -webkit-user-select: none !important;
  -moz-user-select: none !important;
  -ms-user-select: none !important;
  user-select: none !important;
  padding: 0 !important;
  box-sizing: border-box !important;
  display: inline-block !important;
  position: relative !important;
  width: 2.4rem !important;
  height: 2.4rem !important;
  margin: 0 !important;
  border: none !important;
  outline: 0 !important;
  background-color: transparent !important;
}

.swiper-pagination-bullet::before {
  background-color: #646464;
}
.swiper-pagination-bullet::after,
.swiper-pagination-bullet::before {
  content: " ";
  display: block;
  position: absolute;
  top: 15%;
  left: 15%;
  width: 70%;
  height: 70%;
  border-radius: 50%;
  transform: scale(0.5);
  transition: transform 0.225s cubic-bezier(0.5, 0.75, 0.5, 1.65) 0s,
    opacity 0.15s ease-out 0s;
}

.swiper-pagination-bullet::after {
  opacity: 0;
  background-color: #787878;
}

.swiper-pagination-bullet-active::before {
  opacity: 0;
  transform: scale(0.5);
}

.swiper-pagination-bullet:hover::before {
  transform: scale(0.7);
}

.swiper-pagination-bullet-active::after {
  opacity: 1;
  transform: scale(1) !important;
}

.content {
  font-size: 2.08rem;
  color: #fff;
  line-height: 1.65;
}

.content h1 {
  font-size: 4rem;
  font-weight: 700;
}

.content p hr {
  width: 50%;
  background-color: #777;
  border-color: #777;
}

.content p li {
  list-style-type: inherit !important;
}
.content p ul {
  padding-left: 1rem;
  list-style-position: inside;
}

.fancy {
  display: inline-block;
  position: relative;
  padding: 0 0.5rem;
}

.fancy::before,
.fancy::after {
  content: " ";
  position: absolute;
  height: 1.7rem;
  width: 0;
  z-index: 0;
  transition: width 0.325s cubic-bezier(0.65, 0.2, 0, 1);
}

.fancy::before {
  top: 0;
  left: 0;
  background-color: #3a3a3a;
  transition-delay: 0s;
}

.fancy::after {
  bottom: 0;
  right: 0;
  background-color: #3d3d3d;
  transition-delay: 0.1s;
}

.fancy a {
  color: #fff;
  font-weight: 700;
  position: relative;
  z-index: 1;
}

.fancy:hover::before,
.fancy:hover::after {
  width: 100%;
}

.swiper_top_bar {
  background-color: #ddd;
  padding: 0 !important;
  position: relative;
  z-index: 10000;
}

.slider_wrapper_project .swiper-container .img {
  transform: translateY(-0.1rem);
}

.title_project {
  font-weight: 800;
  color: #fff;
  font-size: 6rem;
}

.project_footer {
  line-height: 2;
  color: #8a8a8a;
  margin-top: 5rem;
  text-align: center;
  padding: 2rem 0;
}

.project_footer h4 {
  color: #cacaca;
  font-weight: 700;
  margin-bottom: 0.2rem;
  font-size: 1.8rem;
  line-height: 2;
}

.project_footer p {
  font-size: 1.5rem;
}

@media only screen and (max-width: 800px) {
  .detail_project .container {
    max-width: 100%;
    min-width: 100%;
  }
  .detail_project .container .project_content {
    padding: 1rem;
  }
  .detail_project .container .swiper-slide .img {
    height: 46vw !important;
  }
}
